import * as React from 'react';

import Staff from '@miq/staffjs';
import { Response } from '@miq/utiljs';
import { Loading, Pagination, Viewports } from '@miq/componentjs';

import { TProduct } from '../../types';

import { ProductCardList } from '../components';
import { useProductListRequest } from '../utils';

const ProductUpdateView = React.lazy(() => import('./views.Update'));

export default function ProductUpdateRoute() {
  return (
    <div className="p-update-route d-grid grid-lg-4 gap-2">
      <div className="col main span-lg-3">
        <ProductUpdateView back="/staff/store/products/" />
      </div>

      <Viewports.LGUp>
        <AsideView />
      </Viewports.LGUp>
    </div>
  );
}

const AsideView = () => {
  const { res, loading } = useProductListRequest();

  if (loading) return <Loading />;

  const r = new Response<TProduct>(res);
  if (!r.isSuccess) return null;
  return (
    <div className="col aside pe-2">
      <div className="mb-1 mt-2">{r.isSuccess && <ProductCardList items={r.items} itempath="../" />}</div>

      <div className="pb-4" style={{ position: 'sticky', bottom: 3, overflowX: 'auto' }}>
        <Pagination {...r.data} component={Staff.Link} to />
      </div>
    </div>
  );
};
